.intro{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.intro_title h1{
  font-size: 3rem;
  margin:20px;
  padding:10px 30px;
  border: 5px solid black;
  border-radius:10px;
}
.sliderbtn button{
  color: black;
  font-weight: bolder;
  margin:50px 10px 10px 10px;
  background-color: white;
  border: 1px solid black;
  height: 25px;
  width: 100px;
}
.content_container{
  display: flex;
  margin-top: 40px;
}

.fade{
  animation: fade 2s;
}

@keyframes fade{
  0%{
    transform: translateX(100px);
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
  
}
@media screen and (max-width:900px){
  .content_container{
    display: block;
  }
  .intro_img{
    
  }

}



