.intro_2{
  border: 1px solid black;
  border-radius:10px;
  background-color: white;
  width: 50vw;
  height: 50vh;
  font-size: 1.5rem;
}

.content{
  margin:10px 10px;
  height: 100%;
}
.content h3{
  font-size: 1em;
  margin: 10px 10px;
  font-weight: thin;
  
}
.content p{
  margin-left: 30px;
  font-size: 1em;
}
@media screen and (max-width:900px){
  .intro_2{
    display: block;
    width: 90vw;
    height: 450px;
  }
}