/* 공통 CSS */

.tech{
  width: 95vw;
  height: 100%;
  margin: 20px auto;
}

.tech_title{
  margin: 50px;
  font-size: 1.5rem;
  font-weight: bolder;
  text-align: center;
}

.tech_graph h1{
  font-size: 1rem;
}
.tech_graph{
  flex-basis: 70%;
}
.tech_container{
  display: flex;
  justify-content: left;
}
.container_item{
  margin: 30px 30px;
  position: relative;
}
.item_grahp{
  background-color: skyblue;
}
.item_grahp div{
  width: 10%;
}

.item_title{
  margin-bottom: 10px;
  cursor: pointer;
}

.tech_desc{
  flex-basis: 30%;
  margin-left: 50px;
}
.container_item:hover{
  color:skyblue
}

.container_item:hover .item_grahp{
  color:black
}

.clicknotion:hover{
  color:skyblue;
  cursor:pointer;
}